* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell,
    'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
}

img {
  display: block;
  max-width: 100%;
}
.btn-box {
  display: flex;
  justify-content: center;
}
.btn-box button {
  margin-top: 220px;
}
button {
  display: inline-flex;
  /* margin-top: 220px; */
  margin-right: 10px;
  text-transform: uppercase;
  border: 0;
  background-color: grey;
  color: #fff;
  padding: 20px;
  cursor: pointer;
  font-size: 24px;
  border-radius: 3px;
}

button:disabled {
  background-color: transparent;
  color: darkgrey;
}

#datetime-picker {
  padding: 15px 15px;
  margin-bottom: 40px;
  width: 400px;
  height: 70px;
  border: 1px solid gray;
  border-radius: 5px;
  font-size: 26px;
  outline: transparent;
}

.timer {
  display: flex;
}

.field {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 30px;
}

.label {
  text-transform: uppercase;
  font-family: Roboto Mono, monospace;
  /* background-color: #3f51b5; */
  /* color: #fff; */
  padding: 10px 20px;
  font-size: 20px;
  border-radius: 5px;
}

.value {
  font-family: Roboto Mono, monospace;
  font-size: 80px;
  margin-bottom: 24px;
}

input {
  padding: 15px 15px;
  margin-bottom: 40px;
  width: 200px;
  height: 50px;
  border: 1px solid gray;
  border-radius: 5px;
  font-size: 26px;
  outline: transparent;
}

label {
  text-transform: uppercase;
  font-family: Roboto Mono, monospace;
  font-weight: 600;
}
